"use client"
import React, { useState, useEffect } from "react";
import formatRupiah from "@/utils/formatRupiah";
import TransactionCard from "@/ui/TransactionCard"
import { FaSearch,FaPlus,FaEdit
    ,FaTrash
 } from "react-icons/fa"
import Link from "next/link";
export default function TransactionPage() {
    return (
        <div className="p-4 space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <TransactionCard title="pengeluran hari ini" value={formatRupiah(8000)}/>
            <TransactionCard title="jumlah transaksi hari ini" value={7}/>
            </div>
            <div className="flex flex-col sm:flex-row sm:justify-between sm:items-center gap-4">
                <div className="relative w-full sm:max-w-45">
                <FaSearch className="absolute top-2.5 left-3 text-gray-400"/>
                <input type="text"
                placeholder="cari transaksi"
                className="pl-10 pr-4 py-2 border rounded-md w-full text-sm"
                />
                </div>

                <Link href="/dashboard/transaction/create"
                className="bg-indigo-600 text-white px-4 py-2 rounded-md flex items-center  justify-center gap-2 hover:bg-indigo-700 w-full sm:w-fit ">
                    <FaPlus/> buat transaksi
                </Link>
            </div>
            <div className="bg-white rounded-lg shadow overflow-x-auto">
                <div className="min-w[600px]">
                    <table className="w-full text-sm text-left">
                        <thead>
                            <tr className="text-gray-500 border-b">
                                <th className="p-3">no</th>
                                <th>nama</th>
                                <th>waktu</th>
                                <th>jumlah</th>
                                <th>aksi</th>
                            </tr>
                        </thead>
                            <tbody>
                                <tr className="border-t text-gray-700">
                                    <td className="p-4">1</td>
                                    <td>joran pancing</td>
                                    <td>12-2-2025</td>
                                    <td>{formatRupiah(400000)}</td>
                                    <td>
                                        <div className="flex items-center gap-4">
                                            <Link 
                                            href={'/dashboard/transaction/edit/{id}'}
                                            className="text-indigo-600  px-4 py-2 rounded-md flex items-center justify-center gap-2 hover:text-indigo-800 w-full sm:w-fit< ">
                                            <FaEdit/>
                                            </Link>
                                            <button
                                            className="text-red-600  px-4 py-2 rounded-md flex items-center justify-center gap-2 hover:text-red-800 w-full sm:w-fit< ">
                                            <FaTrash/>
                                            </button>
                                        </div>
                                    </td>
                                </tr>
                            </tbody>
                    </table>
                </div>
            </div>
        </div>
    )
}